<template>
  <div class="moneyIndex" :style="item">
      <Goback title="提现"></Goback>
      <div class="moneyIndex_main">
          <div class="main_box">
              <!-- 银行卡 start -->
              <div class="box_top navigator_arrow" @click="_changePageAddorSelect">
                  <template v-if="is_card">
                      <img src="@/assets/images/add.png" alt="" />
                      <div class="top_text">添加银行卡</div>
                  </template>
                  <template v-else>
                      <div class="top_card">
                          <div>中国工商银行</div>
                          <div class="top_number">6217 0037 6001 3246 806</div>
                      </div>
                  </template>
              </div>
              <!-- 银行卡 end -->
             <div class="box_item">
                 <div class="item_text">提现金额</div>
                 <div class="item_cen">
                     <input 
                     class="cen_inp" 
                     type="number" 
                     v-model="menoy"
                     />
                 </div>
                 <div class="item_fl">
                     <div >可提现余额 {{allmoney}}元 </div>
                     <div class="fl_left" v-show="_setmoney">金额已超过可提现余额</div>
                     <div class="fl_lefts" @click="()=>this.menoy=this.allmoney">全部提现</div>
                 </div>
             </div>
          </div>
          <div class="main_btn" :class="_setmoney||!menoy?'btn_after':''" @click="_changePay">确认提现</div>
      </div>
  </div>
</template>

<script>
export default {
    name:'moneyIndex',
    data(){
        return{
            is_card:true,
            menoy:null,
            allmoney:66.8,
            item:{height:'',width:''}
        }
    },
    computed:{
        _setmoney:function(){
            const { allmoney,menoy } = this
            return menoy>allmoney?true:false
        }
    },
    created(){
        this.item = {
            height: document.documentElement.clientHeight + "px",
            width: document.documentElement.clientWidth + "px"
        };
    },
    methods:{
        _changePay(){
            const { allmoney,menoy } = this
            if(menoy>allmoney) return;
            console.log(1);
        },
        _changePageAddorSelect(){
            this.$router.push({ path:'/moneySelect'})
            // /moneySelect
        }
    }
}
</script>

<style lang="scss">
@import './index.scss';
</style>


